@tailwind base;
@tailwind components;
@tailwind utilities;

@layer components {
  .nav-menu {
    @apply w-64 bg-gray-800 text-white p-6;
  }
  
  .nav-menu h1 {
    @apply text-2xl font-bold mb-8 text-white;
  }
  
  .nav-menu ul {
    @apply space-y-2;
  }
  
  .nav-menu li a {
    @apply block px-4 py-2 rounded hover:bg-gray-700 transition-colors;
  }
  
  .main-content {
    @apply flex-1 p-8 bg-gray-100;
  }

  /* Navigation styles */
  .nav-link {
    @apply flex items-center px-4 py-2 rounded-lg transition-colors duration-200;
  }

  .nav-link-active {
    @apply bg-blue-500 text-white;
  }

  .nav-link-inactive {
    @apply text-gray-600 hover:bg-blue-50;
  }

  .nav-icon {
    @apply w-5 h-5 mr-2;
  }

  /* Modal styles */
  .modal-backdrop {
    @apply fixed inset-0 bg-black bg-opacity-50 flex justify-center items-center z-50 layer-modal;
  }

  .modal {
    @apply bg-white rounded-lg shadow-xl max-w-lg w-full p-6;
  }

  /* Button styles */
  .btn-primary {
    @apply bg-indigo-600 text-white px-4 py-2 rounded-md hover:bg-indigo-700;
  }

  .btn-secondary {
    @apply bg-gray-200 text-gray-700 px-4 py-2 rounded-md hover:bg-gray-300;
    @apply px-3 py-2 rounded shadow-sm border border-gray-300 bg-white hover:bg-gray-50;
    color: var(--accent-color);
  }

  /* Global z-index layers */
  .layer-base {
    z-index: 0;
  }

  .layer-content {
    z-index: 10;
  }

  .layer-overlay {
    z-index: 20;
  }

  .layer-modal {
    z-index: 30;
  }

  .layer-nav {
    z-index: 40 !important;
  }

  /* Apply these classes to your navbar */
  .sticky-nav {
    position: sticky;
    top: 0;
    background: white;
    @apply layer-nav;
  }

  /* Image previews and overlays */
  .image-preview {
    @apply layer-overlay;
  }
}

/* Z-index layering system */
:root {
  --z-base: 1;
  --z-content: 10;
  --z-nav: 100;
  --z-modal: 1000;
  --accent-color: #2563eb;
}

/* Navigation and fixed elements */
.navbar {
  z-index: var(--z-nav);
  position: relative;
}

/* Ensure sticky nav bars always stay on top */
.sticky {
  z-index: 100;
  position: sticky;
  background-color: white;
}

/* Modal overlays */
.modal-overlay {
  z-index: var(--z-modal);
}

/* Image previews and content */
.preview-image {
  z-index: var(--z-overlay);
}

/* Content layers */
.content-layer {
  z-index: var(--z-content);
}

/* Base content */
.base-layer {
  z-index: var(--z-base);
}

.text-accent {
  color: var(--accent-color) !important;
}

.bg-accent {
  background-color: var(--accent-color) !important;
}

.border-accent {
  border-color: var(--accent-color) !important;
}

.hover\:bg-accent:hover {
  background-color: var(--accent-color) !important;
}

.ring-accent {
  --tw-ring-color: var(--accent-color) !important;
}
